:root{
    --color-dark: rgb(14, 14, 14);
    --color-dark2: rgb(32, 32, 32);
    --color-dark3: rgb(22, 22, 22);
    --color-white: #fff;
    --color-green: rgb(103, 195, 80);
    --color-green2: rgb(51, 149, 26);
    --color-red: rgb(184, 40, 35);
    --color-red2: rgb(146, 22, 17);
    --color-gray: rgb(79,79,79);
}

body {
    font-family: "Lato", sans-serif;
    background-color: var(--color-dark);
    color: var(--color-white);
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 4rem;
}
.content {
    display: flex;
    flex-direction: column;
    gap: 4rem;
}

.content-add-item {
    display: block;
    align-items: center;
    gap: 1rem;
    flex-wrap: wrap;
}

#input-new-tesk, 
#input-new-tesk-2,
#btn-new-tesk {
    border: none;
    font-size: 1rem;
    outline: none;
    padding: 1rem;
}

#input-new-tesk, 
#input-new-tesk-2{
    width: 15rem;
    border-radius: 1rem;
}

#btn-new-tesk {
    background-color: var(--color-dark2);
    color: var(--color-white);
    border-radius: 50%;
    width: 50px;
    height: 50px;
    cursor: pointer;
    transition: 400ms;
}#btn-new-tesk:hover{
    background-color: var(--color-dark3);
}

#to-do-list {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    padding: 0;
}

#to-do-list li {
    list-style-type: none;
    font-size: 1.4rem;
    text-transform: capitalize;
    display: flex;
    align-items: center;
    justify-content: space-between;
}#to-do-list li:hover{
    background-color: var(--color-dark3);
    transition: 400ms;
    border-radius: 1rem;
    padding-left: 1rem;
}
#btn-ok{
    border: none;
    background-color: var(--color-red);
    color: var(--color-white);
    font-size: 1rem;
    padding: .4rem .8rem;
    border-radius: 50%;
    width: 40px;
    height: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    transition: 400ms;
}#btn-ok:hover{
    background-color: var(--color-red2);
}